.contact__img
  width: 165px
  height: 224px
  flex-shrink: 0
  margin: 20px auto
  background-color: $gray

.contact__info-text
  font-size: 18px

@media (min-width: $screen-md)
  .contact 
    display: flex
    align-items: center
    padding: 60px 0
  .contact__info-text
    font-size: 22px
  .contact__text
    margin-bottom: 40px
  .contact__img
    margin-right: 30px
    margin-left: 0


@media (min-width: $screen-lg)    
  .contact
    padding: 80px 0 60px   