.intro
    padding: 15px 0
    background-color: $gray
.intro__wrap 
    max-width: $wrapper-width
    margin: 0 auto
    padding: 0 30px
.intro__title
    margin-bottom: 20px
    color: $gray-darken
    font-size: 30px
    font-weight: 700
    text-transform: uppercase
    letter-spacing: 0.07px
.intro__text
    margin-bottom: 10px
    color: $gray-darken
    font-size: 16px
    font-weight: 400
.intro__link
    color: $color-main
    font-weight: bold
    letter-spacing: 0.32px
.intro__img
    margin-top: 20px
.intro__logo
    display: none

@media (min-width: $screen-md)
    .intro
        padding: 60px 0
    .intro__wrap
        display: flex
        flex-wrap: wrap
    .intro__title
        width: 100%
    .intro__text-wrap
        width: 60%
    .intro__img
        width: 40%
    .intro__logo-img
        width: 387px
        height: 26px

@media (min-width: $screen-lg)    
    .intro
        padding: 90px 0 60px   
    .intro__title
        margin-bottom: 40px
        font-size: 51px
        line-height: 64.29px
        &--line
            padding-right: 40%
    .intro__text-wrap
        padding-right: 40px
        padding-bottom: 70px
    .intro__img
        position: relative
        margin-top: -80px
        padding-right: 21px
        z-index: 2
    .intro__logo
        display: block
        padding: 19px 53px
        padding-right: calc(40% + 45px)
        margin-top: -80px
        margin-bottom: 30px
        margin-left: auto
        background-color: $white
