.header
  background-color: $white
  &__wrap
    max-width: $wrapper-width
    margin: 0 auto
    padding-left: 15px
    padding-right: 15px
    padding-top: 20px
    padding-bottom: 5px
    display: block
    position: relative
  &__logo
    display: block
    position: absolute
    top: 35px
    -webkit-transform: translate(0, -50%)
    left: 15px
    justify-content: center
    align-items: center
    max-width: 457px
    width: 70%
    &-link 
      display: block
  &__btn-menu
    margin-right: -10px
    color: $text-color
    font-size: 26px
    border: none
    background: none

  &__nav
    transition: all 1s
    display: none
    flex-direction: column-reverse
    justify-content: center
    text-align: center
  &__nav-main
    &-item
      display: block
      background-color: $white
    &-link
      color: $text-color
      font-weight: 700
      line-height: 24px
      text-transform: uppercase
      letter-spacing: 0.07px  
      &:hover
        color: inherit    
    &-list
      margin: 0
    &-list2
      margin-left: 15px!important
    &-list3
      margin-left: 30px!important
    
  &__nav-second
    display: flex
    flex-direction: column
    margin-bottom: 20px
    .header__nav-second-list
      display: none
    &-item
      display: block
      padding: 0 21px
    &-link
      color: $color-main
      text-transform: uppercase
      font-weight: 700
      
  .slicknav_menu
    background-color: $white
    .slicknav_icon-bar
      background-color: $text-color
  .slicknav_btn
    margin: 0
    padding: 0
    padding-bottom: 15px
    background-color: $white
    color: $color-main
  .slicknav_nav 
    flex-direction: row
    justify-content: space-around
    padding-top: 10px
    a
      color: $text-color
  .slicknav_nav ul
    margin: 0
  .slicknav_item
    display: flex!important
    justify-content: space-between
    align-items: center

@media (min-width: $screen-md)
  .header
    &__wrap
      padding-left: 30px
      padding-right: 30px
      padding-top: 45px
      padding-bottom: 20px
      display: flex
      justify-content: space-between
      align-items: center
      flex-direction: column
    &__logo
      display: flex
      position: static
      margin-bottom: -50px
      img
        width: 100%
    &__nav
      display: flex
      flex-direction: column
      flex-grow: 1
      align-self: stretch
    &__nav-main
      &-list
        display: flex
        margin-left: 10px
      &-item
        display: inline-block
        padding: 16px 2px
        margin-right: auto
        &:last-child
          margin-right: 0
        &:hover
          background-color: $gray
        
    &__nav-second
      flex-direction: row
      justify-content: space-between
      align-items: center
      &-item
        display: inline-block
      &-link
        font-size: 11px
        font-weight: 400
        line-height: 15.89px
        text-transform: uppercase
        letter-spacing: 0.05px
      .header__nav-second-list
        display: block
    &__btn-menu
      display: none
    &__nav-main
      &-list2
        display: none
        position: absolute
        left: -15px
        top: 100%
        z-index: 10
        width: 400px
        .header__nav-main-item
          display: block
          padding: 16px 30px
          text-align: left
          position: relative
      &-item
        &:last-child, &:nth-last-child(2)
          .header__nav-main-list2
            left: auto
            right: 0
        &:last-child
          .header__nav-main-list2
            width: 145px
        // menu lvl 3
        .header__nav-main
          &-list3
            display: none
            position: absolute
            right: 100%
            top: 0
            z-index: 20
            width: 400px
            .header__nav-main-item
              display: block
              padding: 16px 30px
              text-align: left
        &:nth-child(7),&:nth-child(8),&:nth-child(9),&:nth-child(10),&:nth-child(11)
          .header__nav-main-list3
            transform: translateY(-50%)
    .slicknav_menu
      display: none
    
@media (min-width: $screen-lg)
  .header
    z-index: 20
    &__wrap
      flex-direction: row
      align-items: flex-end
      padding-top: 48px
    &__logo
      max-width: 370px
      margin-bottom: 16px
      transform: none
      &-link
        width: 100%
        height: 100%
    &__nav-main-list 
      margin-left: 11%
    &__nav-main-item
      position: relative
    &__nav-main-item:hover
      .header__nav-main-list2
        display: block
        position: absolute
        z-index: 40
        .header__nav-main-item:hover
          .header__nav-main-list3
            display: block
            position: absolute
            z-index: 20
    &__nav-second
      justify-content: flex-end
      margin-bottom: 0
@media (max-width: 1080px) and (min-width: $screen-lg)
  .header__logo
    width: 37%



  
  
    