.header-search
  position: relative
  &__input-group
    height: 21px      
    &-group
      display: flex
      align-items: center
  &__input
    width: 100%
    height: 100%
    border: 1px solid $color-main
    padding: 1px 20px 1px 5px
    &-group
      display: flex
      align-items: center
  &__btn
    position: absolute
    top: 1px
    right: 0
    width: 21px
    height: 21px
    padding: 0
    background: none
    // border: 1px solid $color-main
    border: none
    border-left: none
    border-radius: 0
    color: $color-main

@media (min-width: $screen-md)
  .header-search__input-group
    width: 136px