.footer
  background-color: $color-main
  color: $white
  text-transform: uppercase
  letter-spacing: 0.64px
  padding-top: 54px
  padding-bottom: 40px
  &__wrap
    max-width: $wrapper-width
    margin: 0 auto
    display: flex
    flex-direction: column
    padding-left: 15px
    padding-right: 15px
  &__logo
    max-width: 490px
    width: 100%
    margin-left: auto
    margin-right: auto
    margin-top: -20px
    margin-bottom: 20px
  &__contacts a
    color: $white
    &:hover
      color: $white
  &__tels
    text-align: center
  &__site
    display: block
    text-align: center
  &__email
    display: block
    text-align: center
    margin-bottom: 20px
  &__calc
    display: flex
    justify-content: center
    margin-bottom: 25px
  &__calc-btn
    padding: 10px
    background-color: $gray
    border: none
    color: $color-main
    font-size: 22px
    font-weight: 700
    line-height: 30px
    text-transform: uppercase
    letter-spacing: 0.03px
  &__address
    text-align: center
    &-text 
      margin-bottom: 10px
      &:last-child
        margin-bottom: 0

@media (min-width: $screen-md)
  .footer
    padding-bottom: 40px
    &__wrap
      flex-direction: row
    &__col
      width: 50%
    &__logo
      margin-top: 0
      margin-left: 0
      margin-bottom: 36px
    &__tels, &__site, &__email
      text-align: left
    &__calc
      justify-content: flex-end
      margin-bottom: 50px
      &-btn
        padding: 5px 40px
    &__address
      text-align: right
      &-text
        margin-bottom: 30px
    