*
  box-sizing: border-box
html, body
  overflow-x: hidden
img
  width: 100%
  height: auto
body
  font-family: $font-family
  font-size: $font-size
  color: $text-color
  line-height: $line-height
h1
  font-size: $font-size-h1
  font-weight: 700
  text-transform: uppercase
  letter-spacing: 0.07px 
h3
  font-size: $font-size-h3
  font-weight: 700
  text-transform: uppercase
  letter-spacing: 0.07px 
b
  font-weight: 700
a
  color: $color-main
  &:hover
    color: $color-main-mutted

@media (max-width: $screen-sm)
  h1
    font-size: 30px
    line-height: $line-height