.questions
  position: relative
  padding: 15px 0
  background-color: $gray
  background-image: url("blocks/questions/img/questions-bg-tablet.jpg")
  background-repeat: no-repeat
  background-position: center
  background-size: cover
  overflow: hidden
  &::before
    display: block
    position: absolute
    content: ''
    top: 0
    left: 0
    width: 100%
    height: 2000px
    background-color: $white
    opacity: 0.5
    z-index: 0
      
.questions__wrap
  position: relative
  max-width: $wrapper-width
  margin: 0 auto
  padding: 0 30px
  z-index: 1
.questions__title
  margin-bottom: 20px
  color: $color-main
  font-size: 24px
  font-weight: 700
  text-transform: uppercase
  letter-spacing: 0.07px
.questions__list
  margin-top: 0
  padding-left: 0
  list-style: none
.questions__item
  margin-bottom: 10px
  font-size: 16px
  font-weight: 700
  text-transform: uppercase
  letter-spacing: 0.32px
  &:last-child
    margin-bottom: 0

@media (min-width: $screen-md)
  .questions
    padding: 60px 0
  .question__wrap
    padding: 0 30px
  .questions__title
    margin-bottom: 30px
    padding: 0 30px
    font-size: 24px
  .questions__list
    display: flex
    flex-direction: column
  .questions__item
    box-sizing: border-box
    margin-bottom: 24px
    width: 50%
    &:nth-child(2n)
      align-self: flex-end
    &:nth-child(4n + 1)
      margin-left: 20px
    &:nth-child(4n + 2)
      margin: 0 10px
      margin-bottom: 30px
    &:nth-child(4n + 3)
      margin-right: 20px
      margin-bottom: 0
    &:nth-child(4n)
      margin-top: -15px

@media (min-width: $screen-lg)
  .questions
    padding: 90px 0 350px
    &::before
      display: none
  .question__wrap
    padding: 0 30px
  .questions__title
    padding: 0 200px
    margin-bottom: 72px
    font-size: 48px
    font-weight: 700
    line-height: 61.39px
  .questions__item
    &:nth-child(4n + 1)
      width: 350px
      margin-left: 17%
    &:nth-child(4n + 2)
      width: 280px
      margin-right: 8.5%
      margin-left: 0
      margin-bottom: 30px
    &:nth-child(4n + 3)
      width: 350px
      margin-left: 8.5em
      margin-right: 0
    &:nth-child(4n)
      width: 370px
      margin-top: -20px