.section
  padding: 15px 0
  background-color: $white

.wrap
  max-width: $wrapper-width
  margin: 0 auto
  padding: 0 30px

.pb-22
  padding-bottom: 22px

.mb-5
  margin-bottom: 5px

.mb-44
  margin-bottom: 22px

.bg-gray
  background-color: $gray

.bg-green
  background-color: $color-main

.title
  margin-top: 20px
  margin-bottom: 20px
  font-size: 24px
  font-weight: bold
  text-transform: uppercase

.upp
  text-transform: uppercase

.bold
  font-weight: bold

.bdl-green
  padding-left: 12px
  border-left: 3px solid $color-main

.c-green
  color: $color-main

.c-white
  color: $white

.fz-22
  font-size: 16px

.btn-up
  display: none
  position: fixed
  bottom: 10px
  right: 10px
  width: 40px
  height: 40px
  transform: rotate(-90deg)
  background-color: rgba($gray, 0.5)
  border: none
  border-radius: 5px
  background-image: url("img/arrow.svg")
  background-repeat:  no-repeat
  background-position: center
  background-size: 30px 30px
  font-size: 0
  z-index: 5


@media (min-width: $screen-md)
  .section 
    padding: 60px 0

  .title
    margin-top: 20px
    margin-bottom: 45px
    font-size: 24px
  .fz-22
    font-size: 22px
    
  .mb-44
    margin-bottom: 44px

@media (min-width: $screen-lg)    
  .section
    padding: 90px 0 60px   

  .title
    font-size: 51px

  .btn-up
    right: 50px
    bottom: 50px
    width: 70px
    height: 70px
    border-radius: 10px
    background-size: 50px 50px
