.funct 
  padding: 15px 0
  background-color: $gray

.funct__wrap
  max-width: $wrapper-width
  margin: 0 auto
  padding: 0 30px

.funct__title
  max-width: 470px
  margin-bottom: 20px
  font-size: 24px
  font-weight: 700
  text-transform: uppercase
  letter-spacing: 0.07px

.funct__block
  position: relative
  max-width: 400px
  &::before
    content: ''
    position: absolute
    background-repeat: no-repeat
    background-position: center
    background-size: cover
    z-index: 1

  &:nth-child(7n+1)
    margin-right: 10px
    margin-left: auto
    &::before
      top: 20px
      left: 20px
      width: 420px
      height: 275px
      background-image: url("blocks/funct/img/1.png")
  
  &:nth-child(7n+2)
    margin-left: 20px
    margin-right: auto
    &::before
      top: -10px
      left: -20px
      width: 280px
      height: 420px
      background-image: url("blocks/funct/img/2.png")
  
  &:nth-child(7n+3)
    margin-right: 10px
    margin-left: auto
    &::before
      right: 30px
      width: 420px
      height: 275px
      background-image: url("blocks/funct/img/3.png")

  &:nth-child(7n+4)
    margin-left: 10px
    margin-right: auto
    &::before
      left: 50px
      width: 420px
      height: 275px
      background-image: url("blocks/funct/img/4.png")

  &:nth-child(7n+5)
    margin-right: 30px
    margin-left: auto
    &::before
      left: -40px
      top: 30px
      width: 420px
      height: 275px
      background-image: url("blocks/funct/img/5.png")

  &:nth-child(7n+6)
    margin-left: 10px
    margin-right: auto
    &::before
      bottom: -30px
      width: 420px
      height: 275px
      background-image: url("blocks/funct/img/6.png")

  &:nth-child(7n)
    margin-right: 10px
    margin-left: auto
    &::before
      top: -20px
      right: -20px
      width: 275px
      height: 300px
      background-image: url("blocks/funct/img/7.png")
  

.funct__block-wrap
  position: relative
  padding: 10px
  margin-bottom: 40px
  background-color: $white
  z-index: 2
  
  &:nth-child(7n+2)
    margin-left: 20px

  &:nth-child(7n+3)
    margin-right: 10px


.funct__header
  font-size: 20px
  font-weight: 700
  letter-spacing: 0.44px
  text-transform: uppercase

@media (min-width: $screen-md)
  .funct 
    padding: 60px 0

  .funct__title
    margin-bottom: 63px
    max-width: 470px
    margin-bottom: 63px
    font-size: 51px

  .funct__block
    width: 40%
    max-width: 420px
    margin-bottom: 100px
    &:nth-child(7n+1)
      margin-right: 10px
      margin-left: 0
      &::before
        top: 115px
        right: -160px
        left: auto
    
    &:nth-child(7n+2)
      width: 36%
      margin-left: auto
      margin-right: 14%
      margin-bottom: -234px
      &::before
        top: -225px
        left: auto
        right: -130px
    
    &:nth-child(7n+3)
      margin-right: auto
      margin-left: 6%
      margin-bottom: 9px
      &::before
        right: auto
        left: -70px
        bottom: -230px

    &:nth-child(7n+4)
      margin-left: auto
      margin-right: 17.5%
      margin-bottom: 65px
      &::before
        left: auto
        right: -200px
        bottom: -100px

    &:nth-child(7n+5)
      margin-left: 0
      margin-right: auto
      margin-bottom: 100px
      &::before
        left: auto
        right: -330px
        bottom: -45px
        top: auto

    &:nth-child(7n+6)
      margin-left: 10px
      margin-bottom: -607px
      &::before
        bottom: auto
        left: -70px
        bottom: -83px

    &:nth-child(7n)
      margin-right: 0
      margin-left: auto
      margin-bottom: 0
      &::before                   
        top: -135px
        right: -43px

  .funct__block-wrap
    padding: 30px

@media (min-width: $screen-lg)    
  .funct
    padding: 90px 0 60px   

  .funct__block
    font-size: 22px

    &:nth-child(7n+1)
      &::before
        right: -330px
    &:nth-child(7n)
      &::before 
        top: -290px
        right: 43px
